var actions = require('./actions');
var update = require('react-addons-update');

var initialGameState = {
  randNum: parseInt(Math.random() * (100) + 1),
  userNum: null,
  numHotness: null,
  guessCount: 0,
  guessSet: []
};

var gameController = function(state, action) {
    state = state || initialGameState;
    if (action.type === actions.MAKE_GUESS){

      var diffNum = Math.abs(state.randNum - userNum);
      if (diffNum >= 50){
        numRating = 'Ice cold'
      }
      else if (diffNum < 50 && diffNum >= 30){
        numRating = 'Cold'
      }
      else if (diffNum < 30 && diffNum >= 20){
        numRating = 'Warm'
      }
      else if (diffNum < 20 && diffNum >= 10){
        numRating = 'Hot'
      }
      else if (diffNum < 10 && diffNum >= 1){
        numRating = 'Very hot!'
      }
      else {
        numRating = 'You win!'
      }

      var initSet = state.guessSet.slice();
      var afterSet = initSet.concat(userNum);

      var guessCounter = state.guessCount++;

      var newState = update(state, {
        numHotness: numRating,
        guessSet: afterSet,
        guessCount: guessCounter
      });
    };
    if (action.type === actions.START_NEWGAME){
      return initialGameState;
    };

    return state;
};

exports.gameController = gameController;



